<template>
  <div class="main-wrapper">
    <!--中间的信息部分-->
    <div class="info-wrapper">
      <!--姓名和部门-->
      <el-image class="fzu-icon" :src="require('@/assets/fzu_logo.png')"></el-image>
      <div class="name-wrapper">
        <p class="name">姓名：吴彤</p>
        <p class="name">部门：外国语学院</p>
      </div>
      <!--条形码和二维码-->
      <el-card class="code-wrapper">
        <div style="text-align: center; margin-left: 3%; margin-right: 3%">
          <div class="bar-code-wrapper">
            <el-image class="bar-code" :src="require('@/assets/code1.jpeg')"></el-image>
          </div>
          <div class="time-wrapper">
            <p class="time">{{ month }}月{{ date }}日&nbsp;<span class="second">{{
                second
              }}</span>&nbsp;{{ hour }}:{{ minute }}</p>
          </div>
          <div class="QR-code-wrapper">
            <el-image class="QR-code" :src="require('@/assets/code2.png')"></el-image>
          </div>
          <div class="scroll-word-wrapper">
            <marquee class="scroll-word" behavior="scroll">远志</marquee>
          </div>
          <div class="tip-wrapper">
            <p class="tip">未见异常，允许通行，请主动出示，配合检测，并做好自我防护，出行前请确认。</p>
          </div>
          <div class="one-card-wrapper">
            <el-image :src="require('@/assets/icon.jpg')" class="one-card-icon"></el-image>
          </div>
        </div>
      </el-card>
      <div class="button-wrapper">
        <el-button class="last-button">每日健康填报</el-button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      timer: null,
      time: new Date(),
      month: 0,
      date: 0,
      hour: 0,
      minute: 0,
      second: 0,
    }
  },
  methods: {
    set_time() {
      this.time = new Date();
      let month = this.time.getMonth() + 1;
      let date = this.time.getDate();
      let hour = this.time.getHours();
      let minute = this.time.getMinutes();
      let second = this.time.getSeconds();
      if (month.toString().length < 2) {
        month = '0' + month;
      }
      if (date.toString().length < 2) {
        date = '0' + date;
      }
      if (hour.toString().length < 2) {
        hour = '0' + hour;
      }
      if (minute.toString().length < 2) {
        minute = '0' + minute;
      }
      if (second.toString().length < 2) {
        second = '0' + second;
      }
      this.month = month;
      this.date = date;
      this.hour = hour;
      this.minute = minute;
      this.second = second;
    }
  },
  mounted() {
    this.timer = setInterval(this.set_time, 1000);
  }
}
</script>

